import React from 'react';
import styled from 'styled-components';

const Background = styled.div`
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    
`;
const Container = styled.article`
    width: 1000px;
    text-align: right;
    font-size: 4em;
    font-weight: 800;
    line-height: 1.5em;
`;

export default () => (
    <Background>
        <Container>
            안녕하세요!<br/>
            신입 프론트엔드 개발자 김지용입니다.
        </Container>   
    </Background>
);